import * as calcDisplayState from "../states/calculatorDisplayState.js";
import {possibleInput, operators} from "../states/constants.js";
import * as resultState from "../states/resultState.js";
import * as firstNum from "../states/firstNum.js";
import * as secondNum from "../states/secondNum.js";
import {calcInput} from "../services/elements.js";
import calculate from "../services/calculate.js";
export default function keyboardController() {
  let isFirst = true;
  let operator;
  document.addEventListener("keydown", (evt) => {
    let currentState = calcDisplayState.getState();
    if (possibleInput.includes(evt.key)) {
      let first = firstNum.getState();
      let second = secondNum.getState();
      calcDisplayState.setState(currentState += evt.key);
      if (isFirst && !operators.includes(evt.key))
        firstNum.setState(first += evt.key);
      else if (!isFirst && !operators.includes(evt.key))
        secondNum.setState(second += evt.key);
      calcInput.innerHTML = calcDisplayState.getState();
    }
    if (evt.key === "Backspace")
      calcDisplayState.setState(currentState.replace(/.$/, ""));
    if (operators.includes(evt.key)) {
      operator = evt.key;
      isFirst = false;
    }
    if (evt.key === "Enter") {
      calculate(operator);
      calcInput.innerHTML = "" + resultState.getState();
      firstNum.setState("");
      secondNum.setState("");
      console.log(resultState.getState());
      isFirst = true;
    }
  });
}
