import * as resultState from "../states/resultState.js";
import * as firstNum from "../states/firstNum.js";
import * as secondNum from "../states/secondNum.js";
export default function calculate(operation) {
  const first = parseFloat(firstNum.getState());
  const second = parseFloat(secondNum.getState());
  switch (operation) {
    case "+":
      resultState.setState(first + second);
      break;
    case "-":
      resultState.setState(first - second);
      break;
    case "*":
      resultState.setState(first * second);
      break;
    case "/":
      resultState.setState(first / second);
      break;
  }
}
